import React, { Component } from 'react';
import { Link } from 'react-router';
import isEmpty from 'lodash/isEmpty';
import marked from 'marked';
import './ProjectPage.scss';

import About from './About';

import Content from './Content';
import GridHelper from './GridHelper';
import MediaGallery from './MediaGallery';
import MediaLoaderHelper from './MediaLoaderHelper';
import ProjectHeader from './ProjectHeader';

import projects from '../data/projects';

import backArrow from '../assets/back-arrow.svg';

class Back extends Component {
  render() {
    return(
      <GridHelper className='ProjectPage-Back' size="wide" flex>
        <div className='ProjectPage-BackArrow'><Link to="/"><img src={backArrow} alt='Back arrow'/></Link></div>
        <div className='ProjectPage-BackText'><Link to="/">More stuff</Link></div>
      </GridHelper>
    );
  }
}

class ProjectPage extends Component {
  _getProjectData() {
    const projectRoute = this.props.params.project;
    let project;
    projects.forEach((possibleProject) => {
      const isRoutedProject = projectRoute === possibleProject.route;
      if(isRoutedProject) {
        project = possibleProject;
      }
    });
    return project;
  }
  render() {
    const project = this._getProjectData();
    const hasDetails = !isEmpty(project.details);
    const hasContent = !isEmpty(project.content);
    return (
      <div className='ProjectPage'>
        <Back />
        <GridHelper className='ProjectPage-Header'>
          <ProjectHeader
            title={project.title}
            caption={project.caption}
            key={project.route}
          />
          {hasDetails &&
            <div className='ProjectPage-Details'>
              <div className="ProjectPage-DetailsContent" dangerouslySetInnerHTML={{ __html: marked(project.details) }} />
            </div>
          }
        </GridHelper>
        {hasContent &&
          <Content items={project.content} />
        }
        <About />
        {/*<Back />*/}
      </div>
    );
  }
}

export default ProjectPage;



