import fan from './fan.jpg';
// import programmingObject02a from './02a.mp4';
import programmingObject02 from './02b.mp4';
import programmingObject03 from './03.jpg';
import programmingObject04 from './04.jpg';

export default {
  title: "Programming Objects (2014)",
  route: "programming-objects",
  caption: "What if objects were produced the way open source software is developed?",
  // coverImage: programmingObject01,
  details: "Design Research, Writing",
  content: [
    {
      type: "media",
      content: programmingObject02,
    },
    {
      type: "media",
      content: [programmingObject03, programmingObject04],
    },
    {
      type: "text",
      content: "Creating software has become a flexible, collaborative, and adaptable process: projects developed as code are openly shared, reviewed, adapted, and distributed. Simultaneously, home appliances are increasingly dependent on inflexible standards of production leading to a lack of reparability, less adaptability, and more waste.\n\nWith affordable technologies of digital manufacturing and electronic platforms, translating code into matter is becoming possible for everyone. Programming Objects seek to bring open source software practices into the world of (open hardware) appliances. Moving away from a top-down approach from corporation to consumer, to one where objects are designed, developed, and produced democratically within open communities."
    },
    {
      type: "text",
      content: "<a href='http://www.hackinghouseholds.com' target='_blank'>www.hackinghouseholds.com</a>"
    },
    {
      type: "text",
      content: "with Hacking Households  Leonardo Amico, Thibault Brevet, Jesse Howard, Coralie Gourguechon, Nataa Muevi, Tilen Sepi"
    },
    {
      type: "media",
      content: fan,
    },
    {
      type: "text",
      content: "> In a collaboration initiated at BIO50, I contributed to shaping the project direction, concept definition, prototyping and communication through workshops and sporadic writings."
    },
  ]
}



