import suite from './suite.png';
import journeyUser from './pantheon-journey--user.png';
import journeyData from './pantheon-journey--data.png';
import ecosystem from './pantheon-ecosystem.png';

import suiteOverview from './pantheon-suite--overview.png';
import suiteBackoffice from './pantheon-suite--backoffice.png';
import suiteConcreteNow from './pantheon-suite--concrete-now.png';
import suiteConcrete from './pantheon-suite--concrete.png';
import suiteDataLink from './pantheon-suite--datalink.png';
import suiteDispatchPlus from './pantheon-suite--dispatchplus.png'
import suiteDrive from './pantheon-suite--drive.png'
import suitePulse from './pantheon-suite--pulse.png'
import suiteSalesPlus from './pantheon-suite--salesplus.png'

import data from './pantheon-data.png'

import firstProduct1 from './pantheon-first-product--1.png';
import firstProduct2 from './pantheon-first-product--2.png';
import firstProduct3 from './pantheon-first-product--3.png';
import firstProduct4 from './pantheon-first-product--4.png';
import firstProduct5 from './pantheon-first-product--5.png';
import firstProduct6 from './pantheon-first-product--6.png';

export default {
  title: "Pantheon (2018)",
  route: "pantheon",
  caption: "Guiding a global building material supplier through digital transformation with design",
  // coverImage: programmingObject01,
  details: "User Research, Interaction Design, Software Design @&nbsp;IDEO",
  content: [
    {
      type: "media",
      content: suite
    },

    // (((Challenge & Summary)))
    {
      type: "text",
      content: "Asked to bring a human-centered perspective into the construction industry, we empowered the client's Digital Transformation Office to kickstart their digital transformation process. We created a vision for an ecosystem of digital products, a new organisational structure to  design and build those products, and teamed up with them to launch an MVP of a customer-facing app.",
    },
    {
      type: "text",
      content: "",
    },

    // (((Approach)))
    {
      type: "text",
      content: "## A global user-centered foundation\n\nSince the organisation historically grew by acquiring smaller businesses with different levels of digital fluency from markets all around the world, it now encompasses an extremelly heterogenous range of business processes and IT standards.\n\nTo deal with the complexity, we set out to understand the local realities of a global company. We conducted interviews and co-creation workshops in North America, UK and Australia, with internal stakeholders and their customers (ranging from big to small, from on-site to backoffice and management roles).\n\nLearning about the everyday of the construction industry, interactions within a complex stakeholder landscape and internal organisational constraints enabled us to map out user journeys from early planning to paying the bills.",
    },
    {
      type: "media",
      content: [journeyUser,journeyData],
      caption: "Quote-to-Cash Journey"
    },
    {
      type: "media",
      content: ecosystem,
    },
    {
      type: "text",
      content: "Based on that we envisioned a set of internal and customer-facing digital tools, tightly connected into a data ecosystem, and proposed organisational changes to bring it to life.",
    },
    {
      type: "media",
      content: [suiteOverview,suiteDataLink,suiteBackoffice,suiteConcrete,suiteConcreteNow,suiteDrive,suiteDispatchPlus],
      caption: "The Suite"
    },
    {
      type: "text",
      content: "The suite enables us to unleash more value in the data generated by individual products, as we move from purely displaying information to creating a tightly connected data ecosystem.",
    },
    {
      type: "media",
      content: data,
    },
    {
      type: "text",
      content: "",
    },
    // (((Solution/Benefits & Learnings)))
    {
      type: "text",
      content: "## Launching their first product\n\nIt became apparent fairly quickly that tracking time-sensitive concrete deliveries is a burning global need. The first product is a tool for people on site that handle concrete deliveries, as well as for those managing themto help them be more informed in day-to-day scheduling and decision making.",
    },
    {
      type: "media",
      content: [firstProduct1,firstProduct2,firstProduct3,firstProduct4,firstProduct5,firstProduct6],
      caption: "On-site material delivery tracking app"
    },
    {
      type: "text",
      content: "Joining forces with their internal IT initiatives aiming to become more responsive to customer needs, we set up and integrated into an agile development team for a 7-week sprint to design and release a minimum viable product.\n\nWe piloted the app in 2 regions in North America, starting to unpack both organisational constraints and constraints of their enterprise systems, while engaging the core team and the sales force in the rollout and initial user testing.",
    },
    {
      type: "text",
      content: "> In an interdisciplinary team of four (with addition of interaction, organisational & business design, data science), I led multiple user research streams and designed the vision for a suite of products. I helped set up and embedded into an agile software development team to launch their first product."
    }
  ]
}



