import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './About.scss';

import GridHelper from './GridHelper';

class About extends Component {
  render() {
    return (
      <div className='About'>
        <GridHelper>
          <h1>
            <span>Jure Martinec is a European designer and technologist </span>
            <span>shaping emerging technologies that augment us, </span>
            <span>often  through code and interactive artifacts.</span>
          </h1>
          <h1>
            <span>He currently works at <a href='https://www.ideo.com/' target='_blank'>IDEO</a> and is with <a href='http://www.ansambel.org/' target='_blank'>Ansambel</a>. </span>
            <span><a href='mailto:hello@jure.io'>Get in touch</a> and follow on <a href='https://twitter.com/youremartinets' target='_blank'>Twitter</a>, <a href='https://linkedin.com/in/juremartinec' target='_blank'>LinkedIn</a> or <a href='https://github.com/murej' target='_blank'>GitHub</a>.</span>
          </h1>
        </GridHelper>
      </div>
    );
  }
}

export default About;



