import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router';
import isEmpty from 'lodash/isEmpty';
import './MediaGallery.scss';
import cx from 'classnames';

import GridHelper from './GridHelper';
import MediaLoaderHelper from './MediaLoaderHelper';

class MediaGallery extends Component {
  constructor(props) {
    super(props);

    this.state = {
      index: 0
    }

    this.handleOnItemClick = this._handleOnItemClick.bind(this);
    this.handleArrowClick = this._handleArrowClick.bind(this);
  }
  _handleOnItemClick(i) {
    this._setNextIndex(i);
  }
  _setNextIndex(i) {
    const { items } = this.props;
    const nextIndex = i + 1;
    const isFirstIndex = nextIndex >= items.length;

    const index = isFirstIndex ? 0 : nextIndex;
    console.log(index, items.length);

    this.setState({
      index: index
    });
  }
  _getMediaGallery() {

  }
  _getMedia() {
    let media;
    const { index } = this.state;
    const { items } = this.props;

    const isArray = Array.isArray(items);
    const isSingleArrayElement = isArray && items.length === 1;

    const isSingleMedia = this._isSingleMedia(items);

    const attributes = {
      play: false,
      ratio: 2048/1364,
      volume: 1,
      loop: false,
      controls: false
    }
    if(isSingleMedia) {
      const item = isSingleArrayElement ? items[0] : items;
      media = <MediaLoaderHelper media={item} attributes={attributes} />;
    }
    else {
      media = items.map((item, i) => {
        const className = cx({
          "MediaGallery-Item": true,
          "MediaGallery-Item--active": index === i
        });
        return (
          <div className={className} key={i} onClick={this.handleOnItemClick.bind(this, i)}>
            <MediaLoaderHelper media={item} attributes={attributes} />
          </div>
        );
      });
    }

    return media;
  }
  _getFooter() {
    const { index } = this.state;
    const { items, caption } = this.props;
    const hasCaption = !isEmpty(caption);
    const isSingleMedia = this._isSingleMedia(items);

    return (
      <div className="MediaGallery-Footer">
      {hasCaption && <div className="MediaGallery-Caption">{caption}</div>}
        {!isSingleMedia &&
          <div className="MediaGallery-ArrowContainer">
            <div className="MediaGallery-ArrowBoxMask">
              <div className="MediaGallery-ArrowBox" />
            </div>
            <div className="MediaGallery-ArrowLink" onClick={this.handleArrowClick}>{index+1} of {items.length}<span className="MediaGallery-Arrow">&rarr;</span></div>
          </div>
        }
      </div>
    )
  }
  render() {
    return (
      <div className='MediaGallery'>
        {this._getMedia()}
        {this._getFooter()}
      </div>
    );
  }
  _handleArrowClick() {
    const { index } = this.state;

    this.handleOnItemClick(index);
  }
  _isSingleMedia(items) {
    const isArray = Array.isArray(items);
    const isSingleArrayElement = isArray && items.length === 1;
    const isSingleStringElement = !isArray;

    return isSingleArrayElement || isSingleStringElement;
  }
}
MediaGallery.defaultProps = {
  items: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.string,
  ]).isRequired,
  caption: PropTypes.string
}

export default MediaGallery;



