import hero from './neo-hero.png';
import designEnvironment from './neo-design-environment.png';
import designEnvironmentTools from './neo-design-environment--tools.png';
import evolution from './neo-evolution.png';
import evolutionPaper from './neo-evolution--paper.png';
import evolutionFunctional from './neo-evolution--functional.png';
import expressingIntent from './neo-expressing-intent.png';
import research from './neo-research.jpg';
import extensions from './neo-extensions.png';
import placeholder from '../placeholder.png';

export default   {
  title: "Neo (2017)",
  route: "neo",
  caption: "Building a conversational smart home experience for a global electronics manufacturer",
  // coverImage: x,
  details: "User Research, Interaction Design, Software Design, Development @&nbsp;IDEO",
  content: [
    {
      type: "media",
      content: hero
    },
    // (((Challenge & Summary)))
    {
      type: "text",
      content: "Invited to challenge our client's complex multi-app strategy for their smart home products, we utilised conversational approaches to design a scalable, highly adaptive interface. As we were building the app and continuously testing it with real users, we created an experience that sparks joy and builds trust with early adopters, setting an approach for how an AI-driven platform could mature.",
    },
    {
      type: "text",
      content: "",
    },
    {
      type: "text",
      content: "## Building to learn\n\nConversational User Interfaces are specifically hard to design for without experiencing them first-hand. We started building a fully functional research prototype from day one, deployed it to a small set of test users and continuously conducted in-person research sessions within their homes over a period of 12 weeks.",
    },
    {
      type: "media",
      content: [evolution, evolutionPaper, evolutionFunctional],
      caption: "Evolution of the experience through user research"
    },
    {
      type: "media",
      content: research,
      caption: "User interviews"
    },
    // (((Approach)))
    {
      type: "text",
      content: "The prototype acted as a set of our own custom design tools: a  modular dialog engine and editor for user flows, a natural language processing service to understand context and trigger dialogs, and a functional mobile app experience. \n\nTo further prove early technical feasibility and introduce new ways of prototyping to internal stakeholders, we built on top of their own smart home hardware and software infrastructure with modern technologies and development processes.",
    },
    {
      type: "media",
      content: [designEnvironment,designEnvironmentTools],
      caption: "Our custom-built design environment"
    },

    // (((Solution/Benefits & Learnings)))
    {
      type: "text",
      content: "## Designing for transitional technology\n\nThe experience is designed around 3 main learnings about users: A)&nbsp;exposing boundaries of what is possible teaches new paradigms and builds trust, B)&nbsp;evolving the language to be progressively informal strengthens the relationship over time, and C)&nbsp;additional modes of interaction enrich the conversation (e.g. device buttons in the app or on a wall).",
    },
    {
      type: "media",
      content: expressingIntent,
      caption: "Different ways for the user to express intent or provide information to support the conversation."
    },
    {
      type: "text",
      content: "As we continued designing, these learnings evolved into a set of design principles that explain how users should be guided, supported and captivated when engaging with future conversation-based systems.\n\nWith this approach we enable the smart home to integrate into our environment and the tools around us, wherever it makes most sense for the task at hand. Furthermore, the system can be endlessly extended with additional products and services, even outside of the smart home, with progressive increase in complexity of the experience.",
    },
    {
      type: "media",
      content: extensions,
      caption: "Scaling Neo across the organisation"
    },
    {
      type: "text",
      content: "> In a team of three software designers, I led frontend development and conducted user interviews. I contributed to iterating the experience and defining the design principles.\n\n",
    },
  ]
}



